* {
    margin: 0;
    padding: 0;
    background-color: rgb(255, 255, 255);
}

.Home {
    height: 100%;
    width: 100%;
}


.Body {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width: 100%;
}

.intro {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm 1cm;
    column-gap: 3.5cm;
    margin: 0.5cm;
}

.para {
    font-size: 14px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    /* padding: 0.5px; */
    display: flex;
    flex-direction: column;
    row-gap: 0.2cm;
}

.para1 {
    font-size: 50px;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width: fit-content;
}

.para1:hover {
    background: linear-gradient(to right, #a60643,
            #d05808, #e70fe7, #f6e765, rgb(162, 1, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.para2 {
    font-size: 20px;
    width: 70%;
}

.topic {
    font-size: 20px;
}

.photo {
    padding: 0.2cm 1cm;
}

.p-photo {
    height: 10cm;
    width: 10cm;
    border-radius: 50%;
}

.aboutMe {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 0.2cm;
}

.left {
    display: flex;
    flex-direction: row;
    justify-content: left;
    margin-left: 1cm;
}

.footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0.5cm;
    border-top: thin solid rgb(174, 174, 174);
}

