
.Navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm 1cm;
    border-bottom: thin solid rgb(174, 174, 174);
}

.logo {
    width: 3cm;
    height: 3cm;
    cursor: pointer;
}

.logotext {
    width: 5cm;
    height: 1cm;
    cursor: pointer;
}

.logo-container {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.menu-container {
    display: flex;
    flex-direction: row;
    width: fit-content;
    align-items: center;
    column-gap: .5cm;
    width: fit-content;
}

.menu-items {
    display: flex;
    flex-direction: row;
    column-gap: 2px;
    align-items: center;
    width: fit-content;
}

.item {
    font-size: 16px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.item:hover {
    text-decoration: underline;
    color: #ff00009d;
    font-weight: bold;
}


.About,
.Projects,
.Contact,
.Community {
    height: 1cm;
    width: 1cm;
}